@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,700;0,900;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
}

input {
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400; 
}

button {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
}

.login-form {
  margin-top: 50px;
  display: flex; 
  flex-flow: column wrap; 
  width: 15%;
  margin-left: 42%;
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
}

.signup-form {
  margin-top: 50px;
  display: flex; 
  flex-flow: column wrap; 
  width: 15%;
  margin-left: 42%;
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
}

.navbar-container {
  background-color: #b1aeae;
  display: flex;
  flex-direction: column;
  height: 105vh;
  width: 15%;
}

.avatar {
  max-height: 100px;
}

.nav-button {
  width: 100%;
  margin-top: 3px;
  padding: 2px;
  
}

.dashboard-container {
  background-color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 105vh;
  width: 85%;
  right: 0px;
  top: 0px;
  position: absolute;
}

.add-new-feature-container {
  background-color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 80%;
  right: 0px;
  top: 0px;
  position: absolute;
}




